.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 370px;
  margin-left: auto;
  margin-right: auto;
}
.input {
  width: 100%;
  margin: 0;
  margin-top: 4px;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 40px;
}
.textarea {
  width: 100%;
  height: 120px;
  margin-top: 4px;
  border-radius: 4px;
  padding: 12px 16px;
  margin-bottom: 20px;
  resize: none;
  cursor: pointer;
}